import React from 'react';
import styled from 'styled-components';
import { Section, StretchColumn } from '../common.styled';
import { THEME } from '../../config';

const MAX_WIDTH  = THEME.size.maxWidth.body;

const BannerSection = styled(Section)`
`;
const BannerStretch = styled(StretchColumn)`
  max-width: ${MAX_WIDTH};

  & img {
    width: 100%;
    border-radius: 2em;
    object-fit: cover;
    background-position: center;
  }
`;

export const Banner = ({img}) => { 
  return (
    <BannerSection>
      <BannerStretch>
        <img src={img} alt="banner" />
      </BannerStretch>
    </BannerSection>
  )
}
