import styled from 'styled-components';

const Bars = styled.span`
  width: 2em;
  height: .2em;
  background: ${(props) => (!!props.open ? 'transparent' : (props.color || '#999'))} ;
  display: inline-block;
  position: relative;

  transition: all .2s ease;

  &:before,&:after {
    content: '';
    width: 2em;
    height: .2em;
    background: ${(props) => (props.color || '#999')} ;
    display: inline-block;

    position: absolute;
  }
  &:before {
    top       : ${(props) => (!props.open ? '.5em' : '0')};
    transform : ${(props) => (!props.open ? 'none' : 'rotate(45deg)')};
  }
  &:after {
    top       : ${(props) => (!props.open ? '-.5em' : '0')};
    transform : ${(props) => (!props.open ? 'none' : 'rotate(135deg)')};
  }
`;

const BarsContainer = styled.div`
  display: inline-block;
  width: 2em;
  height: 2em;

  cursor: pointer;
  z-index: 999999;
`;

export const BarsButton = (props) => (
  <BarsContainer>
    <Bars {...props} />
  </BarsContainer>
)

export const LovButton = styled.button`
  height: 2.8em;
  padding: .8em 1.6em;
  font-size: 1em;
  border-radius: .8em;
  border-top-right-radius: 0;
  border: ${(props) => `1px solid ${ props.color?.border || 'black'}`};
  background: ${(props) => `${ props.color?.bg || 'transparent'}`};
  color: ${(props) => `${ props.color?.ft || 'black'}`};
`;
