import React from 'react';
import { BrowserRouter as Router, Route, Redirect } from 'react-router-dom';

import GlobalStyle from './global.styled';
import ExceptionPage from './pages/Exception';
import WelcomePage from './pages/Welcome';

import logo from './logo.svg';
import './App.css';
import * as config from './config';

const LOCAL_URL = config.LOCAL_URL;
const Root = () => {
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.tsx</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    </div>
  );
}

const AppRoute = () => {
  return (
    <>
      <Route path={LOCAL_URL['WELCOME']}                component={WelcomePage} />

      <Route path={LOCAL_URL['ROOT']} exact             component={Root} />
      <Route path={LOCAL_URL['HOME']} exact             component={() => <Redirect to={{pathname: LOCAL_URL['WELCOME']}} /> } />
      <Route path={`${LOCAL_URL['EXCEPTION']}`} exact   component={ExceptionPage} />
      <Route path={`${LOCAL_URL['EXCEPTION']}/:type`}   component={ExceptionPage} />
    </>
  );
}

export default function App () { return (
  <Router >{ /*ref={ref => thisApp.router = ref}> */}
    {/* <ProviderUser superRequest={superRequest} userCache={userCache} url={userUrl}>
      <ProviderEntities superRequest={superRequest}> */}
        <GlobalStyle />
        <Route component={AppRoute}/>
      {/* </ProviderEntities>
    </ProviderUser> */}
  </Router>
)}
